<template>
  <div class="qa-container">
    <el-card title="项目问答助手">
      <el-input 
        v-model="question" 
        placeholder="请输入您的问题..." 
        @keyup.enter.native="submitQuestion"
      ></el-input>
      <el-button 
        type="primary" 
        @click="submitQuestion" 
        style="margin-top: 10px;"
      >
        提问
      </el-button>
      
      <div class="qa-history" v-if="qaHistory.length > 0">
        <div class="qa-item" v-for="(item, index) in qaHistory" :key="index">
          <div class="question">Q: {{ item.question }}</div>
          <div class="answer">A: {{ item.answer }}</div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      question: "",
      qaHistory: []
    };
  },
  methods: {
    submitQuestion() {
      if (!this.question.trim()) return;
      
      this.$axios.post("http://127.0.0.1:5000/api/qa", {
        question: this.question
      }).then(response => {
        this.qaHistory.push(response.data);
        this.question = "";
      }).catch(error => {
        console.error("问答请求失败:", error);
        this.$message.error("获取回答失败，请重试");
      });
    }
  }
};
</script>

<style scoped>
.qa-container {
  padding: 20px;
}

.qa-history {
  margin-top: 20px;
}

.qa-item {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #eee;
  border-radius: 5px;
}

.question {
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}

.answer {
  color: #666;
  white-space: pre-wrap;
}
</style>


